export type GridLayoutMode = 'auto' | 'manual';
export type GridAlignment = 'left' | 'center' | 'right';
export type GridColumnCount = number | GridColumnCountsWithBreakpoint | string;

/**
 * Key value pair where key is the size of the breakpoint in rem or px as string (32px | 2rem) and the value is number of columns.
 */
export interface GridColumnCountsWithBreakpoint {
  [key: string]: number
}
